import React, { useState } from 'react';
import { Checkbox, Form, Input, Col, Row, message } from 'antd';
// import useCurrentUserId from '../../../hook/useCurrentUserId';
import TableData from '../../../react-uikit/component/table/TableData';
import {
  createPortalNews,
  deletePortalNews,
  getPortalNews,
  updatePortalNews,
} from '../../../services/portalNews';
import { dateTimeToString } from '../../../utils/formatUtil';
import { requiredRule } from '../../../utils/validateRules';
import useFormModal from '../../../components/common/FormModal/useFormModal';
import BtnGroup from '../../../components/common/Btn/BtnGroup';
import Btn from '../../../components/common/Btn/Btn';
import Switch2Value from '../../../components/common/Input/Switch2Value';
import DateTimePickerString from '../../../components/common/Input/DateTimePickerString';
import SelectMultiAjax from '../../../components/common/Input/SelectMultiAjax';
import BtnDeleteConfirm from '../../../components/common/Btn/BtnDeleteConfirm';
import { getPortalCategories } from '../../../services/portatCateogry';
import TextEditor from '../../../components/common/Input/TextEditor';
import UploadMultiFile from '../../../components/common/Input/UploadMultiFile/index';
import AvatarUpload from '../../../components/common/Input/AvatarUpload/index';
import { generateFormData } from '../../../utils/formUtil';

const FormItemsSave = ({ form }) => {
  const { getFieldDecorator } = form;

  return (
    <div>
      <Form.Item label="Tiu " className="m-0 required">
        {getFieldDecorator('title', {
          rules: [requiredRule],
        })(<Input />)}
      </Form.Item>
      <Form.Item label="nh i din" className="m-0 required">
        {getFieldDecorator('thumbnailFileId', {
          rules: [],
        })(<AvatarUpload />)}
      </Form.Item>
      <Form.Item label="Ni dung" className="m-0 required">
        {getFieldDecorator('description', {
          rules: [requiredRule],
        })(<TextEditor />)}
      </Form.Item>
      <Form.Item label="Hin th" className="m-0 required">
        {getFieldDecorator('isPublished', {
          rules: [requiredRule],
        })(<Switch2Value />)}
      </Form.Item>
      <Form.Item label="Ngy hin  th" className="m-0 required">
        {getFieldDecorator('startDate', {
          rules: [],
        })(<DateTimePickerString />)}
      </Form.Item>
      <Form.Item label="Chuyn mc" className="m-0 required">
        {getFieldDecorator('categoryIds', {
          rules: [],
        })(<SelectMultiAjax service={getPortalCategories} />)}
      </Form.Item>
      <Form.Item label="Tp nh km" className="m-0 required">
        {getFieldDecorator('attachments', {
          rules: [],
        })(<UploadMultiFile />)}
      </Form.Item>
    </div>
  );
};

export default (props) => {
  // const currentUserId = useCurrentUserId();
  const [reload, setReload] = useState(0);
  const FormSave = useFormModal({
    title: 'Tin tc',
    FormItems: FormItemsSave,
    onSubmit: async (values, { initValue }) => {
      const attachmentIds = (values.attachments || []).map(v => v.id);
      console.log(values);
      const removedAttachmentIds = (initValue.attachments || [])
        .map(v => v.id)
        .filter(id => {
          return !attachmentIds.includes(id);
        });
      const attachments = (values.attachments || []).filter(file => !file.id);
      values.removedAttachmentIds = removedAttachmentIds;
      values.attachments = attachments;
      values.thumbnail = values.thumbnailFileId;
      delete values.thumbnailFileId;
      delete values.thumbnailFileName;
      const formData = generateFormData(values);
      if (values.id) {
        await updatePortalNews(values.id, formData);
        message.success("Cp nht thnh cng");
      } else {
        await createPortalNews(formData);
        message.success("Thm thnh cng");
      }

      setReload(reload + 1);
      // updateStudent(values);
    },
  });
  const handleDelete = async record => {
    await deletePortalNews({ id: record.id });
    setReload(reload + 1);
    message.success("Xa thnh cng");
  };
  const columns = [
    {
      title: 'Tiu ',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: 'Hin th',
      dataIndex: 'isPublished',
      key: 'isPublished',
      width: 120,
      render: text => {
        return <Checkbox checked={text} />;
      },
    },
    {
      title: 'Ngy hin  th',
      dataIndex: 'startDate',
      key: 'startDate',
      width: 160,
      render: text => {
        return dateTimeToString(text);
      },
    },
    {
      title: 'Chuyn mc',
      dataIndex: 'categoryIds',
      key: 'categoryIds',
      width: 160,
      render: (text, record) => {
        return record.categories.map(item => item.label).join(', ');
      },
    },
    {
      title: '',
      dataIndex: 'action',
      key: 'action',
      width: 60,
      render: (text, record) => {
        return (
          <BtnGroup>
            <Btn
              primary
              onClick={() =>
                FormSave.show({
                  initValue: { ...record, categoryIds: record.categories.map(item => item.id) },
                })
              }
            >
              Sa
            </Btn>
            <BtnDeleteConfirm onConfirm={() => handleDelete(record)} />
          </BtnGroup>
        );
      },
    },
  ];
  return (
    <div>
      <Row>
        <Col span={24}>
          <div style={{ float: 'right' }}>
            <Btn
              primary
              onClick={() =>
                FormSave.show({
                  initValue: {},
                })
              }
            >
              Thm
            </Btn>
          </div>
        </Col>
      </Row>
      <TableData
        reload={reload}
        columns={columns}
        paramSearch={props.paramSearch}
        service={getPortalNews}
      />
      <FormSave.Modal />
    </div>
  );
};



