import React, { Component } from 'react';
import { Popconfirm } from 'antd';
import Btn from './Btn'
import './style.scss';

export default class BtnDeleteConfirm extends Component {
  states = '';

  render() {
    return (
      <Popconfirm okText="Xa" cancelText="Hy" title="Xc Nhn Xa ?" {...this.props}>
        <Btn primary style={{  ...(this.props.styleBtn || {}) }}>Xa</Btn>
      </Popconfirm>
    );
  }
}

BtnDeleteConfirm.defaultProps = {
  className: '',
  title: 'Xc Nhn Xa ?',
};



