import React, { Component } from 'react';
import { DatePicker } from 'antd';
import {  dateTimeToStringParamReq, DATE_TIME_SHOW_UI, stringParamReqToDateTime } from '../../../utils/formatUtil';

export default class DateTimePickerString extends Component {
  state = {
    value: null,
  };

  componentWillMount() {
    this.setState({ value: stringParamReqToDateTime(this.props.value) });
  }

  componentWillReceiveProps = nextProps => {
    if (nextProps.value !== this.props.value) {
      this.setState({ value: stringParamReqToDateTime(nextProps.value) });
    }
  };

  handleChange = value => {
    this.props.onChange(dateTimeToStringParamReq(value));
  };
  render() {
    const { value } = this.state;
    return (
      <DatePicker
        format={DATE_TIME_SHOW_UI}
        {...this.props}
        value={value}
        showTime
        onChange={this.handleChange}
      />
    );
  }
}



