import React from 'react';
import {
  Upload,
  Icon,
  message
} from 'antd';
import './style.scss';
import { 
  downloadFile
} from '../../../../services/filestorage';

function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result));
  reader.readAsDataURL(img);
}

function beforeUpload(file) {
  const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
  if (!isJpgOrPng) {
    message.error('Ch c th ti ln tp tin dng JPG/PNG!');
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error('Tp tin ln hn 2MB!');
  }
  return isJpgOrPng && isLt2M;
}

class AvatarUpload extends React.Component {
  state = {
    loading: false,
  };

  componentWillReceiveProps(nextProps) {
    if (typeof nextProps.value === 'string' && nextProps.value !== this.props.value) {
      this.downloadFilebase64(nextProps.value);
    }
  }

  downloadFilebase64 = async (id) => {
    const res = await downloadFile(id);
    getBase64(res.data, imageUrl =>
      this.setState({
        imageUrl,
      }),
    );
    console.log("res");
    console.log(res);
  }

  handleChange = info => {
    // Get this url from response in real world.
    getBase64(info.file.originFileObj, imageUrl =>
      this.setState({
        imageUrl,
      }),
    );
    this.props.onChange(info.file.originFileObj);
  };

  render() {
    const uploadButton = (
      <div>
        <Icon type={this.state.loading ? 'loading' : 'plus'} />
        <div className="ant-upload-text">Upload</div>
      </div>
    );
    const { imageUrl } = this.state;
    return (
      <Upload
        name="avatar"
        listType="picture-card"
        className="avatar-uploader"
        showUploadList={false}
        beforeUpload={beforeUpload}
        onChange={this.handleChange}
        action={null}
      >
        {imageUrl ? <img src={imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
      </Upload>
    );
  }
}


export default AvatarUpload


